/*FUENTES*/

@import "../../../assets/scss/fuentes";

$mapaFuentes: (
  Poppins: $Poppins
);



/*COLORES*/

@import "../../../assets/scss/colores";

$mapaColores: (
  Primary: $Primary,
  PrimaryDark: $PrimaryDark,
  PrimaryLight: $PrimaryLight,
  PrimaryLighten: $PrimaryLighten,
  Text: $Text
);









.aboutMeContainer{
  padding: 4rem 0 2rem;
  max-width: 1024px;
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-right: 1rem;

  .aboutMeContainer__Subtitle{
    text-align: center;
    display: block;
    font-size: 0.75rem;
    font-weight: 600;
  }

  .aboutMeContainer__Title{
    text-align: center;
    font-size: 1-5rem;
    color: $Primary;
    margin-bottom: 1.5rem;
    font-weight: 700;
  }
}









@media screen and (min-width: 576px){
  .aboutMeContainer{
    height: 850px;
    margin-bottom: 12vh;
  }
}





@media screen and (min-width: 768px){
  .aboutMeContainer{
    height: 800px;
    margin-bottom: 0vh;
  }
}





@media screen and (min-width: 1024px){
  .aboutMeContainer{
    margin-left: auto;
    margin-right: auto;
  }
}