/*FUENTES*/

@import "../../../assets/scss/fuentes";

$mapaFuentes: (
  Poppins: $Poppins
);



/*COLORES*/

@import "../../../assets/scss/colores";

$mapaColores: (
  Primary: $Primary,
  PrimaryDark: $PrimaryDark,
  PrimaryLight: $PrimaryLight,
  PrimaryLighten: $PrimaryLighten,
  Text: $Text
);









.projectsContainer{
    padding: 4rem 0 2rem;
    max-width: 1024px;
    width: calc(100% - 2rem);
    margin-left: 1rem;
    margin-right: 1rem;
  
    .projectsContainer__Subtitle{
      text-align: center;
      display: block;
      font-size: 0.75rem;
      font-weight: 600;
    }
  
    .projectsContainer__Title{
      text-align: center;
      font-size: 1-5rem;
      color: $Primary;
      margin-bottom: 1.5rem;
      font-weight: 700;
    }
  }









@media screen and (min-width: 1024px){
  .projectsContainer{
    margin-left: auto;
    margin-right: auto;
  }
}